html, body, #root {
  overflow: hidden;
  height: 100%; /* Ensure full height of viewport is covered */
  width: 100%;
}

body {
  font-family: system-ui;
  margin: 0;
}

*,
*:after,
*:before {
  box-sizing: border-box;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-size: cover; /* Cover the entire container */
  background-position: top center; /* Center the image */
  background-color: #df3f3d; /* Roxo Real */
  padding: 20px;
}
  
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #df3f3ddb;
  min-height: 50vh;
  min-width: 50vh;
  padding: 10px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 40px;
  background-color: #df3f3d;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  font-weight: bold;
  color: #ffffff;
  cursor: pointer;
  box-shadow: 0px 2px 20px rgba(24, 24, 24, 1);
  transition: background-color 0.3s, color 0.3s;
}

.App-header:hover {
  background-color: #c92624; /* Purple background on hover */
}

.App-link {
  color: #61dafb;
}

h2{
  display: flex;
  justify-content: center;
  margin-left: 10px;
}
